		format('woff2');
}

/* 下方是自定义图标演示 */
.myicon {
	font-family: 'myicon' !important;
	font-size: 16px;
	font-style: normal;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.myicon-camera:before {
	content: '\e60f';
}

.myicon-music:before {
	content: '\e617';
}

.myicon-news:before {
	content: '\e618';
}

.myicon-phone:before {
